export default {
  // 授权按钮页面
  name: 'Authorization',
  data() {
    return {};
  },
  methods: {
    getuserinfo(e) {
      console.log('getUserInfo', e);
      if (e.detail.userInfo) {
        console.log('账号信息', e.detail.userInfo);
        uni.setStorageSync('userInfo', e.detail.userInfo);
        this.toPreviousPage();
      }
    }
  }
};
